import styled from "styled-components";
import { useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import { action } from "../../../../../modules/tools";
import iframeComp from "../../../../../utils/iframeComp";

const FPSComponent = () => {
  const dispatch = useDispatch();
  return (
    <FPSPositionBox>
      <FPSComponentBox
        dangerouslySetInnerHTML={iframeComp({
          src: "http://3.36.91.10:9002",
        })}
      ></FPSComponentBox>
      <Link to={"/Languages/CSS"}>
        <BackBtnBox
          onMouseEnter={() => {
            dispatch(action.setCursor("special"));
          }}
          onMouseLeave={() => {
            dispatch(action.setCursor("normal"));
          }}
        >
          <img src="/img/leftArrow.svg" alt="" />
        </BackBtnBox>
      </Link>
      <ReferenceBox>
        <h1> </h1>
        <ul>
          <li>
            <div>
              <div>
                fps    .  
                 .
              </div>
              <div>
                      
                 .
              </div>
              <div>  Enter   .</div>
            </div>
          </li>
          <li>
            <div>
              <div>    .</div>
              <div>
                        
                .
              </div>
              <div>
                       Enter 
                  .
              </div>
              <div>
                     ESC    
                  .
              </div>
            </div>
          </li>
        </ul>
      </ReferenceBox>
    </FPSPositionBox>
  );
};

export default FPSComponent;

const FPSPositionBox = styled.div`
  width: 90%;
  height: 80vh;
  position: relative;
`;

const FPSComponentBox = styled.div`
  height: 100%;
`;

const BackBtnBox = styled.div`
  cursor: none;
  position: absolute;
  left: -50px;
  top: 0px;
  width: 50px;
  height: 50px;
  img {
    animation: bling-bling 0.8s ease-out forwards infinite;
  }
  :hover {
    img {
      animation: hover-animation 0.8s ease-out forwards infinite;
      transition: filter 0.15s linear;
      filter: invert(89%) sepia(20%) saturate(668%) hue-rotate(304deg)
        brightness(102%) contrast(101%);
    }
  }
  @keyframes bling-bling {
    from {
    }
    50% {
      filter: invert(89%) sepia(20%) saturate(668%) hue-rotate(304deg)
        brightness(102%) contrast(101%);
    }
    to {
    }
  }
`;

const ReferenceBox = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 0.8rem;
  background-color: #ffffe8;
  line-height: 2rem;
  text-align: center;
  ul {
    padding: 20px 30px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: left;
    text-align: left;
    list-style: none;
    li {
      padding: 10px 0px;
    }
  }
`;
